.credit-page {
    width: 80%;
    margin: 0 auto 100px;
    text-align: left;
}

.credit-title {
    font-family: LeferiBaseType, sans-serif;
    font-size: 30px;
    margin: 50px 0 20px 0;
}

.creditor {
    font-family: Pretendard, sans-serif;
    font-size: 20px;
    line-height: 30px;
}

.creditor > a {
    text-decoration: none;
    color: #1a48c4;
}

.creditor > a:hover {
    color: #01b1e6;
}

.credit-subtitle {
    font-family: Pretendard, sans-serif;
    font-size: 25px;
    font-weight: 1000;
}